import _tool from '@/util/tools.js';
export default {
	data() {
		return {
			key: '', listData: [],
		}
	},
	onLoad() {

	},
	// 小程序分享
	onShareAppMessage() {
	  return {
	    title: '来自毛衣小说',
	    path: 'pages/book_mall/index'
	  }
	},
	methods: {
		searchBtn() { // 调用小说搜索接口
			let that = this;
			this.zaiApi({
				url:'searchbook.php?search_key=' + that.key,
			}).then(function(res) {
				that.GetSearchData(res)
			});
		},
		GetSearchData(res) { //处理搜索出来的结果
			let that = this;
			_tool.searchData(res).then(function(res) {
				that.listData = res;
			});
		},
		details(id) {
			uni.navigateTo({
				url: '/pages/book_mall/details?id=' + id
			})
		}
	}
}
